@font-face {
    font-family: 'DIN Pro';
    src: local('DIN Pro'), local('DINPro'),
        url-font('DINPro/subset-DINPro.woff2') format('woff2'),
        url-font('DINPro/subset-DINPro.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'DIN Pro';
    src: local('DIN Pro Light'), local('DINPro-Light'),
        url-font('DINPro/subset-DINPro-Light.woff2') format('woff2'),
        url-font('DINPro/subset-DINPro-Light.woff') format('woff');
    font-weight: 300;
    font-style: normal;
}

@font-face {
    font-family: 'DIN Pro';
    src: local('DIN Pro Medium'), local('DINPro-Medium'),
        url-font('DINPro/subset-DINPro-Medium.woff2') format('woff2'),
        url-font('DINPro/subset-DINPro-Medium.woff') format('woff');
    font-weight: 500;
    font-style: normal;
}

@font-face {
    font-family: 'DIN Pro';
    src: local('DIN Pro Bold'), local('DINPro-Bold'),
        url-font('DINPro/subset-DINPro-Bold.woff2') format('woff2'),
        url-font('DINPro/subset-DINPro-Bold.woff') format('woff');
    font-weight: bold;
    font-style: normal;
}

